<h2 id="usage">Usage <a class="header-anchor" href="#usage" aria-hidden="true">&#128279;</a></h2>
<pre v-pre><code v-pre>v-scrollspy:arg=&quot;option&quot;
</code></pre>
<p>Where:</p>
<ul>
<li><code v-pre>arg</code> is the ID (without prefix <code v-pre>#</code>) of the element to scrollspy on (optional), leave it empty to spy on <code v-pre>&lt;body&gt;</code>.</li>
<li><code v-pre>options</code> is an object of configuration (optional).</li>
</ul>
<h3 id="bootstrap-nav-required">Bootstrap nav required <a class="header-anchor" href="#bootstrap-nav-required" aria-hidden="true">&#128279;</a></h3>
<p>Scrollspy currently requires the use of a Bootstrap nav component for proper highlighting of active links.</p>
<h3 id="resolvable-id-targets-required">Resolvable ID targets required <a class="header-anchor" href="#resolvable-id-targets-required" aria-hidden="true">&#128279;</a></h3>
<p>Navbar links must have resolvable id targets. For example, a <code v-pre>&lt;a href=&quot;#home&quot;&gt;home&lt;/a&gt;</code> must correspond to something in the DOM like <code v-pre>&lt;div id=&quot;home&quot;&gt;&lt;/div&gt;</code>.</p>
<h3 id="relative-positioning-required">Relative positioning required <a class="header-anchor" href="#relative-positioning-required" aria-hidden="true">&#128279;</a></h3>
<p>No matter the implementation method, scrollspy requires the use of <code v-pre>position: relative;</code> on the element you're spying on. In most cases this is the <code v-pre>&lt;body&gt;</code>. When scrollspying on elements other than the <code v-pre>&lt;body&gt;</code>, be sure to have a <code v-pre>height</code> set and <code v-pre>overflow-y: scroll;</code> applied.</p>
<h1 id="api-reference">API Reference <a class="header-anchor" href="#api-reference" aria-hidden="true">&#128279;</a></h1>
<h2 id="scrollspy-2"><a href="https://github.com/wxsms/uiv/blob/master/src/directives/scrollspy/scrollspy.js">scrollspy</a> <a class="header-anchor" href="#scrollspy-2" aria-hidden="true">&#128279;</a></h2>
<h3 id="options">Options <a class="header-anchor" href="#options" aria-hidden="true">&#128279;</a></h3>
<div class="table-responsive"><table class="table table-bordered table-hover"><thead>
<tr>
<th>Name</th>
<th>Type</th>
<th>Default</th>
<th>Required</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td><code v-pre>offset</code></td>
<td>Number</td>
<td>10</td>
<td></td>
<td>Pixels to offset from top when calculating position of scroll.</td>
</tr>
<tr>
<td><code v-pre>callback</code></td>
<td>Function</td>
<td></td>
<td></td>
<td><code v-pre>callback(ele)</code> fires whenever a new item becomes activated by the scrollspy, with the element as param.</td>
</tr>
</tbody>
</table></div></markdown-wrapper></template><style>
